@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,700;0,900;1,900&display=swap);
body {  
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #171515;
  font-weight: 700;
  color: #D9D5D5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

*{
    margin: 0;
    padding: 0;
}

.bg-dark{
    text-align: center;
    position: absolute;
    width: auto;
    color: #343434;
    font-size: 14em;
    font-style: italic;
    font-weight: 900;
    padding: 12vw;
    opacity: 0.35;
    z-index: -20;
}

.bg-dark>p{
    margin: -0.4em;
}
.hero{
    color: #D9D5D5;
    font-weight: 700;
    font-size: 3rem;
    text-align: left;
    max-width: 70vw;
    padding: 12vw;
    padding-bottom: 0;
}

.head-msg{
    color: #8A8A8A;
    font-size: 3.5rem;
    width: auto;
    margin-left: -1ch;
    padding-bottom: 0.5ch;
}

.head-msg>span{
    font-weight: 900;
}

.hero-text{
    max-width: 26ch;
}
.prj{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 3rem;
}

.prj-head{
    font-size: 3rem;
    font-weight: 900;
}

.projects{
    text-align: end;
    color: #8A8A8A;
}

.projects>p{
    padding-block: 0.4rem;
    font-size: 1.5rem;
}

